<template>
  <div class="home">
    <div class="head-bar">
      <div class="bar-left">
        <div class="logo" @click.stop="goHome">
          <img src="../../public/mylogo.png" alt="" />
          <span>{{ $t("configuration.crealityPrint") }}</span>
        </div>
      </div>
      <div class="bar-right">
        <!-- 国际化切换 -->
        <div class="interna">
          <i class="iconfont" @click.stop="changeSlect">&#xe65e;</i>
          <el-select
            v-model="value"
            effect="dark"
            class="m-2"
            :placeholder="state.selectVal"
            @change="selectChange"
          >
            <el-option
              v-for="item in state.languageOption"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </div>
        <!-- 重启服务 -->
        <i class="iconfont" @click="restartService">&#xe65b;</i>
      </div>
    </div>
    <!-- 路由形式管控组件 -->
    <router-view />
  </div>
</template>
<script setup>
// import { reactive } from "vue";
import { useRouter } from "vue-router";
import { ElMessage, ElMessageBox } from "element-plus";
import { reactive, getCurrentInstance, onMounted } from "vue";
import { useI18n } from "vue-i18n";
const { locale } = useI18n();
// 唤醒国际化
const { $t } = getCurrentInstance().proxy;
let state = reactive({
  socket: null,
  // headarWidth: "98%",
  // 国际化切换
  languageOption: [
    { value: 0, label: "简体中文" },
    { value: 1, label: "English" },
  ],
  selectVal: "English",
});
onMounted(() => {
  const { proxy } = getCurrentInstance();
  state.socket = proxy.$webSocket;
});
const router = useRouter();
// const menuConfig = reactive({
//   data: [
//     {
//       name: "首页",
//       icon: "",
//       page: "Home",
//       active: true,
//       iconShowInfo: "icon-a-xingzhuang886",
