//       break;
//     case "PrepareAdjust":
//       router.push("/prepare");
//       break;
//     case "PrintHistory":
//       router.push("/printHistory");
//       break;
//     case "DeviceManagement":
//       router.push("/deviceManagement");
//       break;
//     case "ConfigManagement":
//       router.push("/configManagement");
//       break;
//     case "GcodePreview":
//       router.push("/gcodePreview");
//       break;
//     default:
//       break;
//   }
// }
// 动态changeSlect事件
function changeSlect() {
  const elePopper = document.getElementsByClassName("el-popper")[0];
  console.log("elePopper", elePopper.style.display);
  if (elePopper.style.display === "none") {
    elePopper.style.display = "block";
  } else {
    elePopper.style.display = "none";
  }
}
function selectChange(val) {
  // console.log("选的啥", val);
  if (val === 0) {
    state.selectVal = "简体中文";
    locale.value = "zh";
  }
  if (val === 1) {
    state.selectVal = "English";
    locale.value = "en";
  }
  const elePopper = document.getElementsByClassName("el-popper")[0];
  elePopper.style.display = "none";
}
// 返回首页
function goHome() {
  router.push("/home");
}
</script>
<style lang="scss" scoped>
.home {
  width: 100%;
  height: 100%;
  // 兼容移动设备
  @media screen and (max-width: 1200px) {
    .head-bar {
      width: 98% !important;
    }
  }
  .head-bar {
    height: 56px;
    background: #1c1c1d;
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    user-select: none;
    position: fixed;
    z-index: 999;
    width: 1863px;
    // width: 98%;
    .bar-left {
      display: flex;
      align-items: center;
      height: 100%;
      .logo {
        img {
          width: 28px;
          height: 28px;
          cursor: pointer;
        }
        span {
          vertical-align: super;
          padding-left: 10px;
          font-size: 18px;
          font-family: Source Han Sans CN;
          font-weight: bold;
          color: #d2d2d8;
        }
      }
      .line {
        width: 1px;
        height: 20px;
        background: #4b4b50;
        margin: 0 20px;
      }
      .menu {
        display: flex;
        align-items: center;
        height: 100%;
        div {
          padding: 0 20px;
          cursor: pointer;
          height: 100%;
          display: flex;
          align-items: center;
          .tab-span {
            padding-left: 6px;
            color: #b4b4bb;
          }
          .iconfont {
            color: #b4b4bb;
          }
          &:hover {
            background: #343435;
            border-radius: 5px;
            height: 44px;
          }
        }
        .is-active {
          background: $theme;
          height: 44px;
          border-radius: 5px;
          .iconfont {
            color: $white;
          }
          .tab-span {
            color: $white;
          }
        }
      }
    }
    .bar-right {
      font-size: 30px;
      height: 100%;
      writing-mode: vertical-lr;
      margin-right: 30px;
      cursor: pointer;
      .iconfont {
        color: #b4b4bb;
        font-size: 20px;
        &:hover {
          color: #fff;
        }
      }
      .interna {
        padding: 11px 40px 0px 0px;
        writing-mode: horizontal-tb;
        :deep() .el-select {
          --el-select-input-focus-border-color: none !important;
          --el-select-border-color-hover: none !important;
          .select-trigger {
            .el-input {
              width: 105px;
              --el-input-border: none !important;
            }
            .el-input:focus-within {
              border: none;
              .el-input__wrapper {
                .el-input__inner {
                  color: #fff !important;
                }
                // .el-input__suffix {
                //   .el-input__suffix-inner {
                //     .el-icon {
                //       margin-left: -40px;
                //     }
                //   }
                // }
              }
            }
          }
        }
      }
    }
  }
  .content-page {
    padding: 21px;
  }
}
</style>
