  height: 40px !important;
}
.control-main {
  width: 100%;
  height: 218px;
  @include theme_layout();
  .fold {
    cursor: pointer;
    .iconfont {
      color: #d2d2da;
      font-size: 8px;
    }
  }
  // 兼容移动设备
  @media screen and (max-width: 1200px) {
    .right-func {
      .start-func {
        margin-right: 1rem !important;
        button {
          width: 3.4rem !important;
          height: 24px;
