          margin-right: 0.3rem !important;
        }
      }
    }
  }
  .right-func {
    display: flex;
    align-items: center;
    justify-content: center;
    .continue-func {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 5px 10px;
