        &:first-child {
          margin-right: 10px;
        }
        .iconfont {
          margin-right: 5px;
          font-size: 12px;
        }
      }
    }
  }
  // .right-feature {
  //   width: 15rem;
  //   padding: 8px 0px;
  //   display: flex;
  //   justify-content: space-evenly;

  //   button {
  //     width: 100px;
  //     height: 24px;
  //     border-radius: 12px;
  //     font-size: 12px;
  //     .iconfont {
  //       margin-right: 5px;
  //       font-size: 12px;
  //     }
  //   }
  // }
  .containMain {
    @media screen and (max-width: 1200px) {
      .middleContent {
        width: 92% !important;
      }
    }
    .middleContent {
      width: 62%;
      height: 155px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 11px 0px 0px 20px;
      // 兼容移动设备
      @media screen and (max-width: 1200px) {
        .leftPrintshow {
          width: 135px !important;
          img {
            width: 134px !important;
            margin-top: 0 !important;
          }
          #c {
            width: 134px !important;
            left: calc(5% + 20px) !important;
          }
        }
      }
      .leftPrintshow {
        width: 230px;
        height: 135px;
        background: #2b2b2f;
        border-radius: 5px;
        img {
          // width: fit-content !important;
          margin-top: 1rem;
        }
        #c {
          opacity: 0.3;
          width: 10.4%;
          height: 135px;
          position: absolute;
          left: 41px;
        }
      }
      // 兼容移动设备
      @media screen and (max-width: 1200px) {
        .rightPrintshow {
          padding: 0px 0px 0px 15px !important;
          width: 100% !important;
          margin-top: 80px !important;
          .innerFlie {
            height: 25px !important;
            .span2 {
              width: 5rem !important;
            }
          }
        }
      }
      .rightPrintshow {
        width: 400px;
        height: 100px;
        text-align: left;
        padding: 5px 0px 0px 20px;
        .innerFlie {
          width: 100%;
          .span1 {
            width: 24px;
